.witnesscont {
    width: 100%;
    margin: 0 auto;
    li{
        text-decoration: none;
    }
    .witnessinner{        
        padding-top: 160px;
        top: 80px;
        width: 80%;
        margin: 0 auto;
        .witnesslink{
            position: absolute;
            text-decoration-line: none;
            left: 790px;
            top: 150px;
            li{
                float: left;
                font-size: 20px;
                margin-right: 30px;
                font-weight: bold;
            }
        }
        .witnesstop{
            margin-top: 50px;            
            .witnesstitle{
                text-align: left;
                margin-left: 0px;
                margin-bottom: 100px;
                h2{
                    font-size: 30px;
                }
                h3{
                    font-size: 20px;
                }
            }
            .witnessdesc{
                h2{
                    font-size: 30px;
                    color: #3C76B4;
                }
                p{
                    font-size: 20px;
                }
            }
        }
        .witnessgrid{
            display: grid;
            grid-template-columns: repeat(7, 1fr);
            grid-template-rows: repeat(4, minmax(150px, auto));
            // row-gap: 50px;
            text-align: left;
            align-items: center;
            // grid-template-areas: 
