li {
  list-style: none;
}

.headercont{
  z-index: 5;
  position: fixed;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  min-width: 1280px;
  height: 80px;
  text-align: center;
  border-bottom: 1px solid gray;
  background: #fff;
  .headerinner{
    height: 100%;
    width: 1168px;
    margin: 0 auto;
    position: relative;
    .logo{
      position: relative;
      // top: 28px;
      float: left;
      left: 0;
      z-index: 1;
      background-image: url("../../images/maintitle.png");
      width: 180px;
      height: 50px;
      background-size: contain;
      overflow: hidden;
      background-position: center;
      background-repeat: no-repeat;
      img{
        margin: 0;
        padding: 0;
        line-height: 0;
        vertical-align: top;
        border: 0 none;
        background: none;
        object-fit: cover;
      }
    }
    .menu{
      position: relative;
      text-align: center;
      align-items: center;
      .menuname{
        text-align: center;
        align-items: center;        
        position: relative;
        display: inline-block;
        top: 20px;
        padding-left: 100px;
        color: black;
        font-weight: bold;
      }
    }
  }
}