.announcecont{
    width: 100%;
    margin: 0 auto;
    .announceinner{
        padding-top: 160px;
        top: 80px;
        width: 80%;
        margin: 0 auto;
        .announcetop{
            .announcetitle{
                text-align: left;
                margin-left: 0px;
                margin-bottom: 100px;
                h2{
                    font-size: 30px;
                }
            }
        }
        .announcebody{
            display: grid;
            place-items: center;
            grid-template-columns: repeat(3, minmax(366px, auto));
            row-gap: 40px;
            .card{
                border-radius: 10px;
                overflow: hidden;
                width: fit-content;
                width: 366px;
                box-shadow: 0 3px 4px rgba(0, 0, 0, 1);
                .cardimg1{
                    height: 200px;
                    background-image: url("../images/training.jpg");
                    background-size: 100% 100%;
                    background-repeat: no-repeat;  
                    display: flex;
                    align-items: center;      
                    justify-content: center;
                }
                .cardimg2{
                    height: 200px;
                    background-image: url("../images/band.jpg");
                    background-size: 100% 100%;
                    background-repeat: no-repeat;  
                    display: flex;
                    align-items: center;      
                    justify-content: center;
                }
                .cardimg3{
                    height: 200px;
                    background-image: url("../images/sermon.jpg");
                    background-size: 100% 100%;
                    background-repeat: no-repeat;  
                    display: flex;
                    align-items: center;      
                    justify-content: center;
                }                
                .cardtext{
                    height: 160px;
                    padding: 25px;
                    text-align: start;
                    h3{
                        font-size: 20px;
                        font-weight: bold;
                    }
                    span{
                        font-size: 17px;   
                    }
                    p{
                        font-size: 14px;
                        color: #797979;
                    }
                }
            }
        }
    }
}