.footercont{
    padding-top: 200px;
    width: 100%;
    margin: 0 auto;
    .footerinner{
        background-color: #f2f2f3;
        .folink{
            margin-left: 180px;
            .tax{
                cursor: pointer;
                float: left;
                width: 150px;
                height: 100px;
                background-size: contain;
                overflow: hidden;
                background-position: center;
                background-repeat: no-repeat;
                background-image: url("../images/tax.png");
            }
            .people{
                cursor: pointer;
                width: 150px;
                height: 100px;
                background-size: contain;
                overflow: hidden;
                background-position: center;
                background-repeat: no-repeat;
                background-image: url("../images/people.png");
            }
        }
        .fomenu{
            align-items: left;
            text-align: left;
            left: 200px;
            top: 100px;
            position: relative;
            height: 400px;
            .fomenu1{
                float: left;
                margin-right: 130px;
                span{
                    font-size: 20px;
                    font-weight: bold;
                }
                li{
                    margin-top: 20px;
                    font-size: 15px;
                }
            }
        }
        .foinfo{
            position: relative;
            font-size: 12px;
            width: 80%;
            .foinfo1{  
                position: relative;  
                float: left;            
                text-align: left;
                bottom: 30px;
                left: 200px;
            }
            .foinfo2{
                text-align: right;
            }
        }
    }
}