.newscont{
    width: 100%;
    margin: 0 auto;
    .newsinner{
        padding-top: 160px;
        top: 80px;
        width: 80%;
        margin: 0 auto;
        .newstop{
            .newstitle{
                text-align: left;
                margin-left: 0px;
                margin-bottom: 100px;
                h2{
                    font-size: 30px;
                }
            }
        }
        .newsbody{
            display: grid;
            place-items: center;
            grid-template-columns: repeat(3, minmax(366px, auto));
            row-gap: 40px;
            .card{
