.rootcont {
    width: 100%;
    margin: 0 auto;
    li{
        text-decoration: none;
    }
    .rootinner{
        padding-top: 160px;
        top: 80px;
        width: 80%;
        margin: 0 auto;
        .rootlink{
            position: relative;
            text-decoration-line: none;
            left: 600px;
            li{
                float: left;
                font-size: 20px;
                margin-right: 30px;
                font-weight: bold;
            }
        }
        .roottop{
            margin-top: 50px;
            .roottitle{
                text-align: left;                
                margin-left: 150px;
                margin-bottom: 100px;
                h2{
                    font-size: 30px;
                }
                h3{
                    font-size: 20px;
                }
            }
            .rootdesc{
                margin-bottom: 250px;
                h2{
                    font-size: 30px;
                    color: #3C76B4;
                }
                p{
                    font-size: 20px;
                }
            }
        }
        .rootcontent{
            font-size: 20px;
            font-weight: bold;
            line-height: 43px;
        }
    }
}