                            width: 300px;
                            height: 250px;
                        }
                    }
                }
            }         
            h2{
                text-align: left;
                margin-bottom: 90px;
            }
            h3{
                font-size: 20px;
                font-weight: bold;
                text-align: left;
                margin-bottom: 40px;
            }
            li{
                text-align: left;
                font-size: 17px;
            }
