        .books{
            width: 100%;
            h2{
                text-align: left;
            }
            .book1{
                cursor: pointer;
                background-size: 100% 100%;
                background-repeat: no-repeat;  
                display: flex;
                align-items: center;      
                justify-content: center;
                width: 735px;
                height: 200px;
                position: relative;
                float: left;     
                margin-right: 50px; 
                border-radius: 10px;
                background-image: url("../images/book1.png");
            }
            .book2{
                cursor: pointer;
                background-image: url("../images/book2.png");
                background-size: 100% 100%;
