        .radio{
            cursor: pointer;
            margin-top: 100px;
            background-image: url("../images/main2.png");
            background-size: 100% 100%;
            background-repeat: no-repeat;  
            display: flex;
            align-items: center;      
            justify-content: center;
            width: 100%;
            height: 200px;
            position: relative;
            border-radius: 10px;
            li{
                font-size: 60px;
                color: #fff;
                // font-weight: bold;
            }
        }
        .RadioApp{
            cursor: pointer;
            background-image: url("../images/play.png");
            background-size: 100% 100%;
            background-repeat: no-repeat;  
            display: flex;
            align-items: center;      
            justify-content: center;
            width: 300px;
            height: 100px;
            position: relative;
            margin-top: 50px;
            margin-left: 600px;
        }
        .funding{            
            background-color: #3C76B4;            
            width: 100%;
            height: 450px;
            margin-top: 150px;     
            position: relative;
            text-align: left;  
            .arrow_box{
                display: none;
                position: absolute;
                width: 100px;
                padding: 8px;
                top: 400px;
                left: 150px;
                border-radius: 8px;
                background: #333;
                color: #fff;
                font-size: 14px;
            }
            .arrow_box::after{
                position: absolute;
                bottom: 100%;
                left: 50%;
                width: 0;
                height: 0;
                // margin-left: -10px;
                border: solid transparent;
                background-color: rgba(51, 51, 51, 0);
                border-bottom-color: #333;
                border-width: 10px;
                pointer-events: none;
                content: ' ';
            }
            button:hover + p.arrow_box{
                display: block;
            }     
            span{         
                float: left;    
                position: relative;
                top: 60px;   
                left: 120px;
                line-height: 40px;
                // text-align: left;
                font-size: 25px;
                color: #fff;
            }
            li{
                position: relative;                
                line-height: 26px;
                top: 250px;
                right: 620px;
                float: left;
                padding-right: 50px;
                color: #fff;
            }
            button{
                position: relative;
                top: 300px;
                right: 620px;
                background-color: transparent;
                border: 1px solid #fff;
                border-radius: 12px;
                padding: 20px 55px 20px 55px;
                margin-right: 80px;
                font-size: 20px;
                color: #fff;
            }
        }
        .reference{
            margin-top: 120px;
            h2{
                text-align: left;
                margin-bottom: 40px;
            }
            .ref1{
                float: left;
                width: 48%;
                height: 80px;
                font-size: 20px;
                color: #3C76B4;
                background-color: #f2f2f3;
                display: flex;
                align-items: center;      
                justify-content: center;
                border-radius: 12px;
                margin-right: 60px;
                font-weight: bold;
            }
            .ref2{
                width: 48%;
                height: 80px;
                font-size: 20px;
                color: #eab536;
                background-color: #f2f2f3;
                display: flex;
                align-items: center;      
                justify-content: center;
                border-radius: 12px;
                font-weight: bold;
            }
        }
