.findcont {
    width: 100%;
    margin: 0 auto;
    li{
        text-decoration: none;
    }
    .findinner{        
        padding-top: 160px;
        top: 80px;
        width: 80%;
        margin: 0 auto;
        .findlink{
            position: absolute;
            text-decoration-line: none;
            left: 790px;
            top: 150px;
            li{
                float: left;
                font-size: 20px;
                margin-right: 30px;
                font-weight: bold;
            }
        }
        .findtop{
            margin-top: 50px;            
            .findtitle{
                text-align: left;
                margin-left: 0px;
                margin-bottom: 100px;
                h2{
                    font-size: 30px;
                }
                h3{
                    font-size: 20px;
                }
            }
            .finddesc{
                h2{
                    font-size: 30px;
                    color: #3C76B4;
                }
                p{
                    font-size: 20px;
                }
            }
        }
        .findgrid{
            display: grid;
            grid-template-columns: repeat(8, 1fr);
            grid-template-rows: repeat(14, minmax(100px, auto));
            // row-gap: 20px;
            text-align: left;
            align-items: center;
            // margin-bottom: -30px;
            // grid-template-areas: 
