.visioncont {
    width: 100%;
    margin: 0 auto;
    li{
        text-decoration: none;
    }
    .visioninner{
        padding-top: 160px;
        top: 80px;
        width: 80%;
        margin: 0 auto;
        .visionlink{
            position: relative;
            text-decoration-line: none;
            left: 600px;
            li{
                float: left;
                font-size: 20px;
                margin-right: 30px;
                font-weight: bold;
            }
        }
        .visiontop{
            margin-top: 50px;
            .visiontitle{
                text-align: left;
                // margin-left: 20px;
                margin-bottom: 100px;
                h2{
                    font-size: 30px;
                }
                h3{
                    font-size: 20px;
                }
            }
            .visiondesc{

                h2{
                    font-size: 30px;
                    color: #3C76B4;
                }
                p{
                    font-size: 20px;
                }
            }
        }
        .visiongrid{
            display: grid;   
            // grid-template-rows: 1600px;         
            grid-template-columns: repeat(1, 5fr);
            // row-gap: 30px;
            // border: solid 1px black;
            background-color: #f2f2f3;
            border-radius: 12px;
            .visionitem{
                margin-left: 100px;
                text-align: left;
                position: relative;
                h2{
                    font-size: 30px;
                    font-weight: bold;
                    color: #3C76B4;
                }
            }
        }
        .visionbot{
            h2{
                font-size: 30px;
                color: #3C76B4;
            }
            p{
                font-size: 20px;
                line-height: 34px;
                margin-bottom: 40px;
            }
            span{
                font-size: 20px;
                color: #3C76B4;
                line-height: 100px;
                font-weight: 700;
            }
        }
    }
}